import styled from "styled-components";
import React from "react";
import GlobalStyle from "./GlobalStyle";
import Main from "../Main/Main";

export default function () {
  return (
    <Wrapper>
      <GlobalStyle />
      <Main />
    </Wrapper>
  );
}

const Wrapper = styled.div`
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;
