import styled from "styled-components";
import React from "react";
import { data } from "../../../data/data";
import GlobalCard from "../GlobalCard";
import ReactDOM from "react-dom/client";

export default function Replies({ content, person, edit }) {
  const comment = data.comments;
  setTimeout(() => {
    let deletBtn = document.getElementById("deletBtn");
    let editBtn = document.getElementById("editBtn");

    deletBtn.addEventListener("click", (event) => {
      const container =
        event.target.parentNode.parentNode.parentNode.parentNode.parentNode
          ;
      container.setAttribute("id", "deletingContainer");

      setTimeout(() => {
        container.remove()
      }, 550);
    });

    editBtn.addEventListener("click", (event) => {
      const content =
        event.target.parentNode.parentNode.parentNode.children[1].textContent;
      const el =
        event.target.parentNode.parentNode.parentNode.parentNode.parentNode
          .parentNode;
      const rooted = ReactDOM.createRoot(el);
      rooted.render(
        <GlobalCard textarea={content} type="currentUser" score="0" />
      );
    });
  }, 0);

  return edit == null ? (
    content == null || "" ? (
      <>
        {comment.map((comm) =>
          comm.replies.map((response) => (
            <GlobalCard
              type="reply"
              score={response.score}
              image={response.user.image.png}
              username={response.user.username}
              createdAt={response.createdAt}
              content={response.content}
              id={response.id}
            />
          ))
        )}
      </>
    ) : person == null ? (
      <>
        <GlobalCard
          type="reply"
          score="0"
          image={data.currentUser.image.png}
          username={data.currentUser.username}
          createdAt="Now"
          content={content}
          id="0"
        />
      </>
    ) : (
      <div className="replies">
        <GlobalCard
          type="currentUser"
          score="0"
          image={data.currentUser.image.png}
          username={data.currentUser.username}
          createdAt="Now"
          content={content}
          id="0"
        />
      </div>
    )
  ) : null;
}
