import React, { useState } from "react";
import styled from "styled-components";
import ReactDOM from "react-dom/client";
import Replies from "./Replies";
import { data } from "../../../data/data";
import image_juliusomo from "../../../assets/images/avatars/image-juliusomo.png";

const Replying = ({ image, userReply, id: index, edit }) => {
  const [getContent, setGetContent] = useState(userReply);
  const [getEdit, setGetEdit] = useState(edit);

  function typing(event) {
    setGetContent(event.target.value);
  }

  function submitting() {
    var replyData = {
      id: 0,
      content: getContent,
      createdAt: "Now",
      score: 0,
      replyingTo: data.comments.map((comm) =>
        comm.id === index ? comm.user.username : ""
      ),
      user: {
        image: {
          png: image_juliusomo,
        },
        username: "juliusomo",
      },
    };

    data.comments.map((comm) =>
      comm.id === index ? comm.replies.push(replyData) : null
    );
    const main = document.querySelector("main");

    const rooted = ReactDOM.createRoot(document.getElementById("replyingText"));
    rooted.render(<Replies content={getContent} person="currentUser" />);

    setTimeout(() => {
      let cloned = document
        .getElementById("replyingText")
        .firstChild.cloneNode(true);

      console.log(data.comments[0].replies.length);

      data.comments.map((comm) =>
        comm.id === index && comm.replies.length > 0
          ? main.children[index].append(cloned)
          : document
              .getElementById("replyingText")
              .insertAdjacentElement("afterend", cloned)
      );

      document.getElementById("replyingText").remove();
    }, 0);
  }

  return getEdit !== true ? (
    <Wrapper>
      <img src={image} alt="user icon" />
      <textarea onInput={typing} value={getContent} />
      <button id="submitContent" onClick={submitting}>
        Reply
      </button>
    </Wrapper>
  ) : null;
};

const Wrapper = styled.div`
  background-color: white;
  padding: 1em;
  border-radius: 10px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: 100px;
  place-items: top;
  gap: 20px;

  & img {
    max-width: 35px;
  }

  & button {
    padding: 0.5em 1em;
    height: fit-content;
    color: white;
    background-color: hsl(238, 40%, 52%);
    border: 0;
    border-radius: 0.5em;
    font-size: clamp(12px, 2vw, 17px);
  }
  & button:active {
    color: rgba(255, 255, 255, 0.7);
    background-color: hsl(238, 40%, 90%);
  }

  & textarea {
    font-family: "Rubik", sans-serif;
    border-radius: 0.5em;
    padding: 1em 1.2em;
    font-size: clamp(12px, 1.5vw, 17px);
    resize: none;
  }
`;

export default Replying;
