/* eslint-disable default-case */
import React, { useState } from "react";
import ReactDOM from "react-dom/client";
import styled from "styled-components";
import replyImg from "../../assets/images/icon-reply.svg";
import deletImg from "../../assets/images/icon-delete.svg";
import editImg from "../../assets/images/icon-edit.svg";
import { data } from "../../data/data";
import Replying from "./Replies/Replying";

const GlobalCard = ({
  type,
  score,
  image,
  username,
  createdAt,
  content,
  id,
  textarea,
}) => {
  const [changeScore, setChangeScore] = useState(score);
  const [textareaContent, setTextareaContent] = useState(textarea);

  function handleChangeScore(e) {
    var value = e.target.textContent;

    switch (value) {
      case "-":
        if (changeScore > 0) setChangeScore(eval(`${changeScore} ${value} 1`));
        break;
      case "+":
        if (changeScore >= 0 && changeScore < 100)
          setChangeScore(eval(`${changeScore} ${value} 1`));
        break;
    }
  }

  function handleReply(e) {
    var parentContext = e.target.parentNode.parentNode.parentNode.parentNode;
    let getId;
    data.comments.map((comm) => (comm.id === id ? (getId = comm.id) : null));

    const userReplying = `@${e.target.parentNode.children[1].textContent} `;
    const elem = document.createElement("div");
    elem.setAttribute("id", "replyingText");
    elem.style.width = "75%";
    parentContext.insertAdjacentElement("afterend", elem);

    const root = ReactDOM.createRoot(document.getElementById("replyingText"));
    root.render(
      <Replying
        image={data.currentUser.image.png}
        userReply={userReplying}
        id={getId}
      />
    );
  }

  const GlobalContent = () => {
    return (
      <>
        <div className="rate">
          <button onClick={handleChangeScore}>+</button>
          <p>{changeScore}</p>
          <button onClick={handleChangeScore}>-</button>
        </div>
        <div className="userComment" id={id}>
          <div className="topContent">
            <img src={image} alt="user icon" />
            <h2>{username}</h2>
            <p>{createdAt}</p>
            <button onClick={handleReply}>Reply</button>
          </div>
          <p>{content}</p>
        </div>
      </>
    );
  };
  const onTyping = (event) => {
    setTextareaContent(event.target.value);
  };

  const btns = document.querySelectorAll("button");
  btns.forEach((element) => {
    setTimeout(() => {
      element.addEventListener("click", (e) => {
        if (document.querySelector(".replies").innerHTML === "") {
          document.querySelector(".replies").remove();
        }
      });
    }, 500);
  });

  function CurrentUserContent() {
    return (
      <>
        <div className="rate">
          <button onClick={handleChangeScore}>+</button>
          <p>{changeScore}</p>
          <button onClick={handleChangeScore}>-</button>
        </div>
        <div className="userComment" id={id}>
          <div className="topContent">
            <aside>
              <img src={data.currentUser.image.png} alt="user icon" />
              <h2>{data.currentUser.username}</h2>
              <div>You</div>
              <p>Now</p>
            </aside>
            <aside>
              <button id="deletBtn">Delete</button>
              <button id="editBtn">Edit</button>
            </aside>
          </div>
          {textarea || textarea === "" ? (
            <textarea
              onChange={onTyping}
              onBlur={(e) => e.target.select()}
              value={textareaContent}
            />
          ) : (
            <p>{content}</p>
          )}
        </div>
        {textarea || textarea === "" ? (
          <button id="updateBtn" onClick={onUpdate}>
            UPDATE
          </button>
        ) : null}
      </>
    );
  }

  function onUpdate(e) {
    var parentContext = e.target.parentNode.parentNode.parentNode;
    let root = ReactDOM.createRoot(parentContext);
    root.render(
      <GlobalCard
        type="currentUser"
        score="0"
        content={textareaContent}
        id="0"
      />
    );
    setTimeout(() => {
      let deletBtn = document.getElementById("deletBtn");
      let editBtn = document.getElementById("editBtn");

      deletBtn.addEventListener("click", (event) => {
        const container =
          event.target.parentNode.parentNode.parentNode.parentNode.parentNode
            .parentNode;
        container.setAttribute("id", "deletingContainer");

        setTimeout(() => {
          container.remove();
        }, 550);
      });

      editBtn.addEventListener("click", (event) => {
        const content =
          event.target.parentNode.parentNode.parentNode.children[1].textContent;
        const el =
          event.target.parentNode.parentNode.parentNode.parentNode.parentNode
            .parentNode;
        const rooted = ReactDOM.createRoot(el);
        rooted.render(
          <GlobalCard textarea={content} type="currentUser" score="0" id="0" />
        );
      });
    }, 0);
  }

  switch (type) {
    case "comm":
      return (
        <Wrapper>
          <Comm>
            <GlobalContent />
          </Comm>
        </Wrapper>
      );

    case "reply":
      return (
        <Wrapper>
          <Reply>
            <GlobalContent />
          </Reply>
        </Wrapper>
      );

    case "currentUser":
      return (
        <Wrapper>
          <Reply>
            <CurrentUserContent />
          </Reply>
        </Wrapper>
      );
  }
};

const Reply = styled.article`
  width: 75%;
`;

const Comm = styled.article`
  width: 100%;
`;

const Wrapper = styled.div`
display: flex;
justify-content: flex-end;
align-items:center;
width: 100%;
height: 200px;

  article {
    display: grid;
    grid-template-columns: auto 1fr;
    padding 1em;
    min-height: 150px;
    background-color: white;
    border-radius: 10px;

    &:has(#updateBtn){
      row-gap: 10px;
      
      & #updateBtn{
        background-color: hsl(238, 40%, 52%);
        border: 0px;
        color: white;
        border-radius: 5px;
        padding:.5em 1em;
        width: min-content;
        margin-left: auto;
        grid-area: 2 / 2 / 3 / 3;
    }
    
    & textarea{
    border-radius: 5px;
    resize: none;
    font-family: "Rubik", sans-serif;
  font-size: clamp(12px, 1.5vw, 17px);
  padding: .5em 1em;
  
  }
}
  }
  .rate {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0em 1em 0 1em;
    height: min-content;
    text-align: center;
    background-color: hsl(239, 57%, 95%);
    color: hsl(238, 40%, 52%);
    border-radius: 10px;
    padding: 0.2em 0.5em;
    font-size: 1.2em;
    gap: 7px;

    & button {
      background-color: transparent;
      border: none;
      color: hsl(238, 40%, 52%);
      font-size: 1.5em;
      width: 100%;
    }
  }

  .userComment {
    display: grid;
    grid-template-rows: auto 1fr auto;
    grid-template-columns: 100%;
    font-size: clamp(12px, 2vw, 17px);
    gap:10px;
    height: 100%;

    & p:last-child {
      color: rgba(0, 0, 0, 0.5);
      line-height: 25px;
      word-break: break-word;
    }

    & .topContent {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 20px;

      &:has(aside){
        display: flex;
        justify-content: space-between;
        align-items: center;

        & aside:nth-child(2){
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 50px;
        }

        & aside:nth-child(1){
          display: grid;
          grid-template-columns: auto 1fr auto auto;
          gap: 10px;
          place-items: center;

          & p{
          margin-left: 10px;
          }
        }

        & h2 {
          margin-left: 10px;
        }

        #deletBtn{
          color: hsl(357, 100%, 70%);
        }

        #deletBtn::before{
          background: url('${deletImg}');
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          width: 70%;
          height: 70%;
          background-repeat: no-repeat;
          background-size: contain;
          object-fit:cover;
          left: -40%;
        }

        & button:active{
         opacity: .5;
        }

        #editBtn::before{
          background: url('${editImg}');
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          width: 70%;
          height: 70%;
          background-repeat: no-repeat;
          background-size: contain;
          object-fit:cover;
          left: -50%;
        }
      }

      & div:nth-child(3){ 
        padding: .2em .5em;
        color: white;
        border-radius: 2px;
        font-size: clamp(12px, 2vw, 17px);
        background-color: hsl(238, 40%, 52%);
      }

      & p:nth-child(3) {
        margin-right: auto;
        color: rgba(0, 0, 0, 0.5);
      }

      & h2 {
        font-weight: bold;
      }

      & img {
        max-width: 35px;
        object-fit: cover;
      }

      & button{
        position: relative;
        background-color: transparent;
        border: none;
        color: hsl(238, 40%, 52%);
        font-size: 1.1em;
        font-weight: 600
      }

      &:has(not:(aside)) button::before{
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 70%;
        height: 70%;
        background: url('${replyImg}');
        background-repeat: no-repeat;
        background-size: contain;
        object-fit:cover;
        left: -50%
      }
    }
  }
`;

export default GlobalCard;
